.footer{
  padding: 50px 0 150px;
  &__top{
    display: flex;
    margin-top: 2px;
  }
  &__group{
    max-width: 325px;
    margin-top: 10px;
    &:not(:last-child){
      margin-right: 90px;
    }
  }
  &__subtitle{
    opacity: 0.5;
    letter-spacing: -0.6px;
    margin-bottom: 2px;
  }
  &__subtitle,
  &__desc{
    font-size: 18px;
    line-height: 144%;
  }
  &__mail{
    color: #012783;
  }
  &__logo{
    display: block;
    width: 388px;
    margin-left: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 79px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__c{
    font-size: 16px;
    line-height: 150%;
    opacity: 0.5;
    letter-spacing: -0.5px;
  }
  .nav{
    padding: 0;
  }
}
