.header{
  position: relative;
  overflow: hidden;
  &__logo,
  &__subtitle,
  &__title,
  &__desc,
  &__btn{
    position: relative;
    z-index: 2;
  }
  &__logo{
    display: block;
    margin-top: 53px;
    width: 321px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__subtitle{
    margin-top: 115px;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
  }
  &__title{
    font-weight: 300;
    font-size: 90px;
    letter-spacing: 0.02em;
    font-family: $font2;
    b{
      font-weight: 600;
    }
  }
  &__desc{
    margin-top: 20px;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    letter-spacing: 1px;
    color: $dark;
  }
  &__btn{
    margin-top: 32px;
    width: 137px;
    height: 50px;
  }
  &__img{
    max-width: calc(100% - 250px);
    position: absolute;
    top: 0;
    right: 0;
    path:not(.not-anim){
      animation: 5s ease-in-out 0s infinite triangleFade;
    }
    svg{
      max-width: 100%;
      height: auto;
    }
  }
  &__track{
    display: block;
    margin: -80px 0 0 -190px;
    max-width: 100%;
  }
  &__inner{
    display: flex;
    align-items: center;
    padding: 48px 0 24px;
  }
  &__back{
    margin-left: -80px;
  }
  &__logo2{
    margin-left: 85px;
    width: 349px;
    img{
      width: 100%;
      display: block;
    }
  }
}

@keyframes triangleFade {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100% { opacity:0; }
}

.nav{
  display: flex;
  align-items: center;
  padding: 58px 0;
  &__link{
    color: $primary;
    font-size: 16px;
    line-height: 150%;
    &:not(:last-child){
      margin-right: 20px;
    }
    &:hover{
      color: $dark;
    }
  }
}
