.display1{
  font-weight: 600;
  font-size: 120px;
  letter-spacing: 0.02em;
  color: $dark;
  font-family: $font2;
}

.display2{
  font-weight: 600;
  font-size: 80px;
  letter-spacing: 0.02em;
  color: $dark;
  font-family: $font2;
}

.display3{
  font-weight: 400;
  font-size: 48px;
  line-height: 125%;
  font-family: $font2;
}

.body-text{
  font-size: 20px;
  line-height: 150%;
  color: rgba($black,.8);
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
}

.accent-text{
  color: $primary;
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}
