.error{
  padding: 135px 0 50px;
  &__subtitle{
    max-width: 565px;
  }
  &__desc{
    margin-top: 45px;
    a{
      color: $primary;
      &:hover{
        text-decoration: underline;
        color: $dark;
      }
    }
  }
}


.copyright{
  padding-top: 80px;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 49.4% auto;
  }
  &__desc{
    margin-top: 30px;
    color: #2F323C;
  }
  &__desc2{
    margin-top: 48px;
    color: #2F323C;
    p{
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
  }
  &__img{
    img{
      margin: -20% 0 0 -20%;
      width: 139.3%;
      display: block;
    }
  }
}
