.info{
  position: relative;
  z-index: 2;
  margin-top: -150px;
  &__inner{
    display: grid;
    grid-template-columns: 62px auto 73px;
    gap: 80px;
  }
  &__left,
  &__right{
    width: 100%;
  }
  &__content{
    margin-left: auto;
    text-align: right;
    max-width: 920px;
  }
  &__desc{
    max-width: 755px;
    margin: 20px 0 0 auto;
  }
}

.help{
  margin-top: 210px;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 47.2% auto;
    gap: 44px;
  }
  &__desc{
    margin-top: 20px;
    max-width: 731px;
  }
  &__img{
    img{
      width: 116.3%;
      display: block;
    }
  }
}

.world{
  margin-top: 72px;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 33.8% auto;
    gap: 0;
  }
  &__content{
    padding-top: 38%;
  }
  &__desc{
    margin-top: 24px;
  }
  &__img{
    img{
      margin-left: -10.7%;
      width: 129.1%;
      display: block;
    }
  }
}

.about{
  margin-top: 100px;
  &__inner{
    display: grid;
    grid-template-columns: 62px auto 51.8% 73px;
    gap: 5px;
  }
  &__content{
    text-align: right;
    padding-top: 5.8%;
    padding-right: 9.5%;
  }
  &__desc{
    margin: 24px 0 0 auto;
    max-width: 700px;
    p{
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
  }
  &__img{
    width: 92%;
    margin: 15.5% 0 0 7.5%;
  }
}

.why{
  margin-top: 210px;
  overflow: hidden;
  &__inner{
    margin-top: -10%;
    display: grid;
    grid-template-columns: 49.1% auto;
    gap: 0;
    align-items: center;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__subtitle{
    margin-top: 32px;
    max-width: 685px;
  }
  &__desc{
    margin-top: 32px;
    p{
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
  }
  &__img{
    img{
      margin-left: -23%;
      width: 152.5%;
      display: block;
    }
  }
}

.price{
  padding-bottom: 50px;
  &__inner{
    display: grid;
    grid-template-columns: auto 41.5%;
    gap: 20px;
  }
  &__desc{
    margin-top: 20px;
  }
  .fg{
    margin-bottom: 30px;
  }
  &__btn{
    margin-top: -6px;
    width: 229px;
    height: 60px;
    font-size: 20px;
    border-color: rgba($black,.8);
  }
}
